.sliderCoupon{
    width: 100%;
    display: block;
    padding: 60px;
    background-color: rgb(255, 255, 255);
    position: relative;
    border-bottom: 1px solid rgb(241, 241, 241);
    padding-left: 70px;
}
.imgCoupon{
    width: 386px;
    height:180px;
}
@media(min-width :768px) and (max-width:1024px) {
    .imgCoupon{
    width: 219px;
    height: 180px;
    }
  }
  
  @media (max-width: 764px) {
      .sliderCoupon{
          display:none;
      }
  }